.switch{position:relative;display:inline-block;width:100px;height:34px}.switch input{display:none}.slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#c5c5c5;-webkit-transition:.4s;transition:.4s;border-radius:34px}.slider:before{position:absolute;content:"";height:26px;width:26px;left:4px;bottom:4px;background-color:#fff;-webkit-transition:.4s;transition:.4s;border-radius:50%}input:checked+.slider{background-color:#2f5a53}input:checked+.slider:before{-ms-transform:translateX(26px);transform:translateX(66px)}.slider:after{content:"ВЫКЛ";color:#fff;display:block;position:absolute;transform:translate(-50%, -50%);top:50%;left:50%;font-size:10px;font-family:Verdana,sans-serif}input:checked+.slider:after{content:"ВКЛ"}